import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validationBatalTimbang from "../Validations/validationBatalTimbang";
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class BatalPengambilanTimbang extends Component {
  state = {};
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-md-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="noBatal"
                    label="Nomor Batal"
                    type="text"
                    component={renderField}
                    normalize={(value) => value && value.toUpperCase()}
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="tanggal"
                    label="Tanggal"
                    type="date"
                    component={renderField}
                    normalize={(value) => value && value.toUpperCase()}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

BatalPengambilanTimbang = reduxForm({
  form: "BatalPengabilanTimbangan",
  validate: validationBatalTimbang,
  enableReinitialize: true,
})(BatalPengambilanTimbang);
export default connect()(BatalPengambilanTimbang);
